@import url(https://fonts.googleapis.com/css?family=Roboto);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

:root{--primary-color: #dc3545;--dark-color: #333333;--light-color: #f4f4f4;--danger-color: #dc3545;--success-color: #28a745}*{box-sizing:border-box;margin:0;padding:0}body{font-family:'Roboto', sans-serif;font-size:1rem;line-height:1.6;background-color:#fff;color:#333}a{color:#dc3545;color:var(--primary-color);text-decoration:none}a:hover{color:#666}ul{list-style:none}img{width:100%}.container{max-width:1100px;margin:auto;overflow:hidden;padding:0 2rem}.x-large{font-size:4rem;line-height:1.2;margin-bottom:1rem}.large{font-size:3rem;line-height:1.2;margin-bottom:1rem}.lead{font-size:1.5rem;margin-bottom:1rem}.text-center{text-align:center}.text-primary{color:#dc3545;color:var(--primary-color)}.text-dark{color:#333333;color:var(--dark-color)}.text-success{color:#28a745;color:var(--success-color)}.text-danger{color:#dc3545;color:var(--danger-color)}.text-center{text-align:center}.text-right{text-align:right}.text-left{text-align:left}.all-center{display:flex;flex-direction:column;width:100%;margin:auto;justify-content:center;align-items:center;text-align:center}.card{padding:1rem;border:#ccc 1px dotted;margin:0.7rem 0}.list{margin:0.5rem 0}.list li{padding-bottom:0.3rem}.p{padding:0.5rem}.p-1{padding:1rem}.p-2{padding:2rem}.p-3{padding:3rem}.py{padding:0.5rem 0}.py-1{padding:1rem 0}.py-2{padding:2rem 0}.py-3{padding:3rem 0}.m{margin:0.5rem}.m-1{margin:1rem}.m-2{margin:2rem}.m-3{margin:3rem}.my{margin:0.5rem 0}.my-1{margin:1rem 0}.my-2{margin:2rem 0}.my-3{margin:3rem 0}.grid-2{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:1rem}.grid-3{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:1rem}.grid-4{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1rem}.btn{display:inline-block;background:#f4f4f4;background:var(--light-color);color:#333;padding:0.4rem 1.3rem;font-size:1rem;border:none;cursor:pointer;margin-right:0.5rem;-webkit-transition:opacity 0.2s ease-in;transition:opacity 0.2s ease-in;outline:none}.btn-link{background:none;padding:0;margin:0}.btn-block{display:block;width:100%}.btn-sm{font-size:0.8rem;padding:0.3rem 1rem;margin-right:0.2rem}.badge{display:inline-block;font-size:0.8rem;padding:0.2rem 0.7rem;text-align:center;margin:0.3rem;background:#f4f4f4;background:var(--light-color);color:#333;border-radius:5px}.alert{padding:0.7rem;margin:1rem 0;opacity:0.9;background:#f4f4f4;background:var(--light-color);color:#333}.btn-primary,.bg-primary,.badge-primary,.alert-primary{background:#dc3545;background:var(--primary-color);color:#fff}.btn-light,.bg-light,.badge-light,.alert-light{background:#f4f4f4;background:var(--light-color);color:#333}.btn-dark,.bg-dark,.badge-dark,.alert-dark{background:#333333;background:var(--dark-color);color:#fff}.btn-danger,.bg-danger,.badge-danger,.alert-danger{background:#dc3545;background:var(--danger-color);color:#fff}.btn-success,.bg-success,.badge-success,.alert-success{background:#28a745;background:var(--success-color);color:#fff}.btn-white,.bg-white,.badge-white,.alert-white{background:#fff;color:#333;border:#ccc solid 1px}.btn:hover{opacity:0.8}.bg-light,.badge-light{border:#ccc solid 1px}.round-img{border-radius:50%}input{margin:1.2rem 0}.form-text{display:block;margin-top:0.3rem;color:#888}input[type='text'],input[type='email'],input[type='password'],input[type='date'],select,textarea{display:block;width:100%;padding:0.4rem;font-size:1.2rem;border:1px solid #ccc}input[type='submit'],button{font:inherit}table th,table td{padding:1rem;text-align:left}table th{background:#f4f4f4;background:var(--light-color)}.navbar{display:flex;justify-content:space-between;align-items:center;padding:0.7rem 2rem;z-index:1;width:100%;opacity:0.9;margin-bottom:1rem}.navbar ul{display:flex}.navbar a{color:#fff;padding:0.45rem;margin:0 0.25rem}.navbar a:hover{color:#f4f4f4;color:var(--light-color)}.navbar .welcome span{margin-right:0.6rem}@media (max-width: 700px){.hide-sm{display:none}.grid-2,.grid-3,.grid-4{grid-template-columns:1fr}.x-large{font-size:3rem}.large{font-size:2rem}.lead{font-size:1rem}.navbar{display:block;text-align:center}.navbar ul{text-align:center;justify-content:center}}

